.searchWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.searchInput {
  width: 100%;
  height: 30px;
  padding: 5px;
  border-radius: 5px;
}

button {
  padding: 5px;
  background-color: transparent;
  cursor: pointer;
  border: 1px solid lightgrey;
  margin-top: 10px;
}

button:active {
  background-color: #333;
  border-color: #333;
  color: #eee;
}
